
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,
InlineLoading
},
data() {
return {
imgsrc: true,
dataobj: {},
};
},
mounted() {
this.initdata();
},
methods: {
getimgsrc(imgsrc){
return this.$root.hostImage+imgsrc

},
initdata() {
this.$ajax.post(this.$root.host, {
'MSH.1': 'drugpool',
'MSH.2': 'getDrug',
'cbm': this.$route.params.code,
'token':this.$root.loginInfo.token,
}).then(data => {
console.log(data)
if (typeof data != 'undefined' && Object.keys(data).length > 0) { //判断是否空对象

this.dataobj = data;
this.imgsrc = data.CYPDT || false;
} else {
this.dataobj = {};
}
}).catch(error => {
this.imgsrc = false;
});
},
}
};



